* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: Roboto, Arial;
}

html {
   scroll-behavior: smooth;
}

body {
   min-width: 1650px;
   overflow-x: hidden;
}

img {
   display: block;
}


/* Выбор через ЛКМ */
::selection {
   background-color: rgb(139, 0, 0); 
}


/* Полоса прокрутки */
::-webkit-scrollbar {
   width: 10px;
}

::-webkit-scrollbar-track {
   background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
   background: #1F3A93;
}

::-webkit-scrollbar-thumb:hover {
   background: rgb(25, 47, 117);
}


/* Прелоудер */
.preloader_css {
   display: block;
   position: fixed;
   z-index: 99999;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   min-width: 1000px;
   background: #1F3A93 url(http://hello-site.ru//main/images/preloads/bars.svg) center center no-repeat;
   background-size: 67px;
}


/* Шапка */
header {
   display: flex;
   justify-content: flex-end;
   align-items: center;
   padding: 30px 70px;
   height: 90px;
   background: white;
   position: fixed;
   left: 0;
   right: 0;
   z-index: 9999;
}

.logo {
   position: absolute;
   top: 12px;
   left: 15%;
   width: 372px;
   height: 100%;
   background-image: url('img/covid.png');
   background-size: 300px 65px;
   background-repeat: no-repeat;
}

header p {
   margin-right: auto;
   color: white;
   font-size: 30px;
   font-family: Montserrat;
}

.nav_links a {
   color: black;
   transition: .5s;
   text-decoration: none;
}

.nav_links li {
   display: inline-block;
   padding: 0 20px;
   font-size: 18px;
   text-transform: uppercase;
   font-family: Montserrat;
}

.nav_links li a {
   transition: .5s;
}

.nav_links a:hover {
   color: rgb(0, 140, 255);
   transition: .5s;
}


