/* Смайлики */
.covid_and_faces {
   cursor: url('img/cursor.png') 30 30, crosshair;
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   width: 100%;
   background-image: url('./img/smiles.jpg');
}

.covid_and_faces .face {
   position: relative;
   width: 300px;
   height: 300px;
   border-radius: 50%;
   background: #ffcd00;
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 0 20px;
}

.covid_and_faces .face:hover {
   background: linear-gradient(180deg, #f44336, #ffcd00)
}

.covid_and_faces .face:before {
   position: absolute;
   content: '';
   width: 150px;
   height: 70px;
   border-bottom-left-radius: 70px;
   border-bottom-right-radius: 70px;
   background: #b57700;
   transition: 0.5s;
   top: 180px;
}

.covid_and_faces .face:hover:before {
   top: 210px;
   width: 150px;
   height: 20px;
   border-bottom-left-radius: 0px;
   border-bottom-right-radius: 0px;
   background: #9d0b00;
}

.covid_and_faces .face .eyes {
   position: relative;
   display: flex;
   flex-wrap: wrap;
   top: -40px;
}

.covid_and_faces .face .eyes .eye {
   position: relative;
   width: 80px;
   height: 80px;
   display: block;
   margin: 0 15px;
   background: #fff;
   border-radius: 50%;
}

.covid_and_faces .face .eyes .eye:before {
   content: '';
   position: absolute;
   top: 50%;
   left: 25px;
   transform: translate(-50%, -50%);
   width: 40px;
   height: 40px;
   background: #333;
   border-radius: 50%;
}

.covid_and_faces h1 {
   color: white;
   font-size: 64px;
   position: absolute;
   bottom: 15%;
   user-select: none;
}


/* Подвал */
footer {
   padding-top: 23px;
   height: 130px;
   background: black;
   position: relative;
   bottom: 0;
   left: 0;
   right: 0;
   text-align: center;
   font-size: 20px;
}

button {
   width: 220px;
   height: 50px;
   border: none;
   outline: none;
   color: #fff;
   background: #111;
   cursor: pointer;
   position: relative;
   z-index: 0;
   border-radius: 10px;
   font-size: 14px;
   font-family: Montserrat;
   font-weight: 400;
}

button:after {
   z-index: -1;
   content: '';
   position: absolute;
   width: 100%;
   height: 100%;
   background: #111;
   left: 0;
   top: 0;
   border-radius: 10px;
}

button:before {
   content: '';
   background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
   position: absolute;
   top: -2px;
   left: -2px;
   background-size: 400%;
   z-index: -1;
   filter: blur(5px);
   width: calc(100% + 4px);
   height: calc(100% + 4px);
   animation: glowing 20s linear infinite;
   opacity: 0;
   transition: opacity .3s ease-in-out;
   border-radius: 10px;
}

button:hover:before {
   opacity: 1;
}

button:active {
   color: white;
}

button:active:after {
   background: transparent;
}

@keyframes glowing {
   0% {
      background-position: 0 0;
   }

   50% {
      background-position: 400% 0;
   }

   100% {
      background-position: 0 0;
   }
}

.copyright {
   color: gray;
   font-size: 16px;
   margin: 10px;
